<template>
    <div>
        <div class="projectblock" :style="{top:top+'%',left:left+'%',zIndex:zindex}">
            <img class="bgpic" :src="bgpic" alt="">
            <div class="open" @click="tolink">
                <img src="../assets/Group 150.svg" alt="">
            </div>
        </div>
        <div :class="'projectblockmobile projectblockmobile'+this._uid">
            <div class="topbar">
                <span>{{name}}</span>
                <div :class="'arrow-down arrow-down'+this._uid" @click="open"></div>
            </div>
            <div class="content">
                <img :class="'mobilebgpic mobilebgpic'+this._uid" :src="bgpic" alt="">
                <div :class="'linkblock linkblock'+this._uid">
                    <span @click="tolink">link</span>
                </div>
            </div>
            <!-- <img class="bgpic" :src="bgpic" alt="">
                                <div class="open" @click="tolink">
                                    <img src="../assets/Group 150.svg" alt="">
                                </div> -->
        </div>
    </div>
</template>

<script>
import anime from 'animejs/lib/anime.es.js';
export default {
    props: ['projectpic', 'topprops', 'leftprops', 'mobileleftprops', 'mobiletopprops', 'projectlink', 'projectname', 'propsindex'],
    data() {
        return {
            bgpic: this.projectpic,
            top: this.topprops,
            left: this.leftprops,
            mobiletop: this.mobiletopprops,
            mobileleft: this.mobileleftprops,
            link: this.projectlink,
            isopen: false,
            name: this.projectname,
            arrowname: 'arrow-down' + this._uid,
            projectblockname: 'projectblockmobile' + this._uid,
            picname: 'mobilebgpic' + this._uid,
            linkname: 'linkblock' + this._uid,
            zindex: this.propsindex
        }
    },
    methods: {
        tolink() {
            window.open(this.link)
        },
        open() {
            if (this.isopen == false) {
                console.log(this.arrowname, this.projectblockname);
                anime({
                    targets: '.' + this.projectblockname,
                    height: '350px',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.arrowname,
                    rotate: '180deg',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.picname,
                    height: '250px',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.linkname,
                    opacity: 1,
                    easing: 'easeInOutQuad'
                });
                this.isopen = true;
            } else {
                anime({
                    targets: '.' + this.projectblockname,
                    height: '39px',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.arrowname,
                    rotate: '360deg',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.picname,
                    height: '0px',
                    easing: 'easeInOutQuad'
                });
                anime({
                    targets: '.' + this.linkname,
                    opacity: 0,
                    easing: 'easeInOutQuad'
                });
                this.isopen = false;
            }

        }
    }
}
</script>

